import React from 'react';
import styled from 'styled-components';

const Footer = styled.footer`
    padding: 10px 100px;
    text-align: center;
    font-size: 12px;
    color: #aaa;
`;

function Component() {
    return (
        <Footer>
            <h1>Footer</h1>
        </Footer>
    );

}

export default Component;